import React,{Component} from 'react';

class GoogleMap extends Component{
    componentDidMount(){
        new window.google.maps.Map(this.refs.map,{
            zoom:8,
            center:{
                lat:this.props.lat,
                lng:this.props.lon
            }
        })
    }
    render(){
        return(
            <div ref="map" />
        )
    }
}

export default GoogleMap;


